<script>
    import Page from "../components/Page.svelte";

    import teamMembers from '../data/teamMembers';
    import MemberCard from "../components/MemberCard.svelte";

    function sorter(a, b) {
        if (a.last_name < b.last_name) {
            return -1;
        }
        if (a.last_name > b.last_name) {
            return 1;
        }
        return 0;
    }

    const pageInfo = {
        headerText: 'Our Team',
        text: 'We are compassionate people working together for a higher purpose, seeking to create value for clients and humanity with a spirit of excellence.'
    };
</script>

<Page {...pageInfo}>
    <div class="team-surface w-max-n1 m-auto flex--wrap flex-row--tc">
        {#each teamMembers.sort(sorter) as member}
            <MemberCard {...member}/>
        {/each}
    </div>
</Page>

