<script>
    import {mediaManager} from '../responsiveController';
    import {onMount} from 'svelte';

    let open = false;

    let barFlex;
    let linkSize;
    let linkFlex;
    let linkHeight = '';
    determineDynamicStyles()

    onMount(() => {
        const body = document.getElementsByTagName('body')[0];
        body.addEventListener('click', (e) => {
            if (e.target === e.currentTarget) {
                e.stopPropagation();
            }
            open = false;
        }, true);
    });

    function determineDynamicStyles() {
        const breaks = mediaManager.breaks;
        barFlex = mediaManager.active <= breaks[3] ? "flex-col--cc" : "flex-row--cc";
        linkFlex = mediaManager.active <= breaks[2] ? "flex-col--cc" : "flex-row--cc flex--space-around";
        linkSize = '2vw';
        if (mediaManager.active < breaks[2]) {
            linkSize = '4vw';
        } else if (mediaManager.active < breaks[3]) {
            linkSize = '3vw';
        }
        if (mediaManager.active < mediaManager.breaks[2] && !open) {
            linkHeight = 0;
        } else {
            linkHeight = ''
        }

        if (mediaManager.active < mediaManager.breaks[3] && !open) {
            linkHeight = 0;
        } else {
            linkHeight = ''
        }
    }

    $: {
        if (mediaManager.active < mediaManager.breaks[3] && !open) {
            linkHeight = 0;
        } else {
            linkHeight = ''
        }
    }


    mediaManager.addEventListener('change', () => {
        determineDynamicStyles()
    });

    const links = [
        'Mission',
        'Capabilities',
        'Platforms',
        'Team',
        'Work',
        'Clientele'
    ]

</script>

<div class="top-bar-surface sticky p-l1 {barFlex}">
    <div class="logo-box relative m-auto">
        <a href='#home'><img class="pt-n3" src='./spin_logo.png' alt='SPIN Logo'></a>
    </div>
    <div class="of-hidden w-v90 m-auto {linkFlex}" style="height: {linkHeight}">
        {#each links as link}
            <a href='#our_{link.toLowerCase()}'>
                <h2 class="font_header-secondary">{link}</h2>
            </a>
        {/each}
    </div>
    {#if mediaManager.active < mediaManager.breaks[3] && !open}
        <div class="button-surface absolute">
            <a href="#/login"><img class="avatar" src="./hamburger.png" alt="avatar"/></a>
        </div>
    {/if}
</div>

<style>
    .button-surface {
        top: 50px;
        right: 50px;
        z-index: 1001;
    }

    .top-bar-surface {
        top: 0;
        z-index: 9998;
        background: var(--color-primary3);
        box-shadow: 0 0 16px rgba(0, 0, 0, 0.5);
    }

    .logo-box {
        background: var(--color-primary3);
    }

    img {
        min-width: 96px;
        max-width: 256px;
    }
</style>
