<script>
    import Mission from "../components/Mission.svelte";
    function calcScrollPct(height, scrollAmt) {
        if (scrollAmt < 0) return 0;
        if (scrollAmt > height) return height;
        return scrollAmt / height;
    }

    const lerp = (x, y, a) => x * (1 - a) + y * a;
    const clamp = (a, min = 0, max = 1) => Math.min(max, Math.max(min, a));
    const genRange = (a, b, c) => (x) => clamp(lerp(a, b, x), a, c || b);
    const range = genRange(-80, 90);

    let scrollY;
    let height;

</script>

<svelte:window bind:scrollY={scrollY} bind:innerHeight={height}/>

<div class="splash-surface flex-col--cc">
    <h1 class="font_header-primary absolute w-max-n2" style="color: white;">
        Disrupting disasters, from neighborhoods to nations
    </h1>
</div>
<div class="parallax-positioner absolute" style="top: {-range(calcScrollPct(height, scrollY))}vh;">
    <div class="parallax-surface m-auto br-px8 w-p95 w-max-n2 m-auto">
        <Mission/>
    </div>
</div>

